@import url("https://fonts.googleapis.com/css?family=Barlow:300,400,500,600,700&display=swap");
//Fonts
$font-01: "Barlow", sans-serif;

// Main Colours
$m-01: #c2509c;
$m-02: #9e6aff;

//Neutral Colours
$neu-01: #f5f5f5;
$neu-02: #eaeaea;
$neu-03: #ebebeb;
$neu-04: #b7b7b7;
$neu-05: #666a76;
$neu-06: #404145;
$neu-07: #1c1c1e;
$neu-08: #202842;

// Gradients
$g-01: linear-gradient(45deg, $m-01, $m-02);

@mixin lg {
  @media screen and (max-width: 1440px) {
    @content;
  }
}

@mixin md {
  @media screen and (max-width: 1100px) {
    @content;
  }
}

@mixin sm {
  @media screen and (max-width: 799px) {
    @content;
  }
}

@mixin xs {
  @media screen and (max-width: 512px) {
    @content;
  }
}

@mixin xxs {
  @media screen and (max-width: 375px) {
    @content;
  }
}

// Mixins for Styles
@mixin delay {
  @for $i from 1 through 100 {
    &:nth-child(#{$i}) {
      animation-delay: #{$i * 0.15}s;
    }
  }
}

// Animations
@keyframes enterLeft {
  0% {
    opacity: 0;
    transform: translateX(-100%);
  }
}

@mixin anime {
  opacity: 0;
  transform: translateY(160px);

  &.anime {
    animation: itemsFade 1s ease forwards;

    @keyframes itemsFade {
      0% {
        opacity: 0;
        transform: translateY(160px);
      }

      100% {
        opacity: 1;
        transform: translateY(0px);
      }
    }
  }
}

$cubic: cubic-bezier(0.76, 0.57, 0.17, 1.05);

* {
  margin: 0;
  padding: 0;
  list-style: none;
  border: 0;
  -webkit-tap-highlight-color: transparent;
  text-decoration: none;
  color: inherit;
  box-sizing: border-box;

  &:focus {
    outline: none;
  }
}

::-webkit-scrollbar {
  width: 0px;
}

::-moz-selection {
  background-color: $m-02;
  color: #ffffff;
}

::selection {
  background-color: $m-02;
  color: #ffffff;
}

body {
  font-family: $font-01;
  // background-color: #202842;
}

.spacing {
  height: 100vh;
}

.noscroll {
  overflow: hidden;
}

.loader {
  width: 100vw;
  height: 100vh;
  background-color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  z-index: 5;

  &.remove {
    display: none;
  }

  &__content {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }

  &__item {
    width: 140px;
    height: 12px;
    display: flex;
    background-color: white;
    border-radius: 8px;
    position: relative;
    overflow: hidden;
    box-shadow: inset 0px 0px 0px 2px $m-02,
      0 3px 9px -2px rgba(0, 48, 135, 0.4);
    z-index: 1;

    &:before {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-color: $m-02;
      border-radius: inherit;
      animation: loading 3s cubic-bezier(0.57, 0.48, 0.21, 1.23) infinite;

      @keyframes loading {
        0% {
          width: 16px;
          left: 0;
        }

        25% {
          width: 100%;
          left: 0;
        }

        50% {
          width: 16px;
          left: calc(100% - 16px);
        }

        75% {
          width: 100%;
          left: 0;
        }

        100% {
          width: 16px;
          left: 0;
        }
      }
    }
  }

  &__text {
    margin-top: 24px;
    font-size: 16px;
    line-height: 26px;
    letter-spacing: 0.2px;
    color: $neu-05;
    animation: text 3s cubic-bezier(0.57, 0.48, 0.21, 1.23) infinite;

    @keyframes text {
      0% {
        opacity: 0.1;
      }

      50% {
        opacity: 1;
      }

      100% {
        opacity: 0.1;
      }
    }
  }
}

.link {
  position: relative;
  font-weight: 600;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  position: relative;
  transition: all 0.2s ease;

  &:before {
    content: "";
    position: absolute;
    width: calc(100% + 8px);
    height: 2px;
    background-color: $m-02;
    transform: translateY(2px);
    transition: all 0.2s ease;
  }

  &:hover {
    &:before {
      width: calc(100% + 8px);
    }
  }

  &.anime {
    &:before {
      animation: textBefore 1s 1s ease both;

      @keyframes textBefore {
        0% {
          width: 0%;
        }
      }
    }
  }
}

.mainNav {
  user-select: none;
  color: white;
  width: 100%;
  padding: 0 40px;
  box-sizing: border-box;
  position: fixed;
  z-index: 4;

  @include sm {
    padding: 0 8px;
  }

  &__content {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 80px;

    @include sm {
      height: 60px;
    }
  }

  &:before {
    content: "";
    position: absolute;
    width: 100%;
    height: 100%;
    left: 0;
    top: 0;
    backdrop-filter: blur(5px);
    background-color: rgba(239, 241, 248, 0.7);
    opacity: 0;
    z-index: -1;
    transition: all 0.4s ease;
    transform: translateY(-100%);
    box-shadow: 0px 2px 10px rgba(black, 0.2);
  }

  &.sticky-out {
    &:before {
      opacity: 0;
      transform: translateY(-100%);
    }
  }

  &__logo {
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    width: 140px;
    height: 46px;
    background-image: url("./assets/logos/logo-w.svg");
    background-repeat: no-repeat;
    background-size: contain;
    background-position: center;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all 0.2s ease;
    font-size: 0px;

    &:before {
      content: "";
      position: absolute;
      width: 0px;
      height: 2px;
      background-color: $m-02;
      transform: translate(8px, 2px);
      transition: all 0.2s ease;

      @include sm {
        display: none;
      }
    }

    &:hover {
      &:before {
        width: calc(100% - 12px);
      }
    }

    @include sm {
      margin-left: 5px;
    }

    @include xs {
      max-width: 40px;
      max-height: 40px;
      background-image: url("./assets/logos/logo-mobile-w.svg");
      margin-right: 6px;
      margin-left: 5px;
    }
  }

  &__links {
    display: flex;

    @include md {
      display: none;
    }
  }

  &__linkItem {
    letter-spacing: 1.5px;
    font-size: 16px;
    text-transform: uppercase;
    font-weight: 400;
    margin-right: 32px;
    position: relative;
    padding: 2px 4px;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all 0.2s ease;

    &:before {
      content: "";
      position: absolute;
      width: 0px;
      height: 2px;
      background-color: $m-02;
      transform: translateY(2px);
      transition: all 0.2s ease;
    }

    &:hover {
      &:before {
        width: calc(100% + 4px);
      }
    }

    &:last-child {
      margin-right: 0px;
    }
  }

  &__actions {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 6px;
  }

  &__search {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 20px;
    position: relative;

    @include sm {
      margin-right: 10px;
    }

    @include xs {
      margin-right: 6px;
    }

    input {
      background-color: rgba(white, 0.9);
      width: 100%;
      padding-left: 16px;
      padding-right: 30px;
      border-radius: 20px;
      color: $neu-08;
      height: 35px;
      transition: all 0.2s ease;

      @include xs {
        background-color: rgba(white, 0.8);
      }

      &::placeholder {
        font-family: $font-01;
        font-size: 14px;
        letter-spacing: 1px;
        font-weight: 400;
        transition: all 0.2s ease;
      }

      &:focus {
        &::placeholder {
          opacity: 0.5;
          letter-spacing: 2px;
          transform: translateX(5%);
        }
      }
    }
  }

  &__searchBtn {
    position: absolute;
    right: 10px;
    background-color: transparent;
    width: 20px;
    height: 20px;
    transition: all 0.3s ease;
    cursor: pointer;

    img {
      width: 16px;
      height: 16px;
      margin-top: 1px;
    }

    &:hover {
      transform: scale(1.02);
      opacity: 0.6;
    }
  }

  &__upload {
    margin-right: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    min-width: 40px;
    height: 40px;
    border-radius: 50%;
    position: relative;
    cursor: pointer;
    transition: all 0.4s ease;
    opacity: 0.8;

    svg {
      width: 25px;
      height: 25px;

      &:hover {
        opacity: 0.5;
      }

      g path {
        fill: $m-02;

        @include sm {
          fill: white;
        }
      }
    }

    @include sm {
      margin-right: 10px;
    }

    @include xs {
      margin-right: 6px;
    }
  }

  &__profile {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    cursor: pointer;

    @include sm {
      margin-right: 7px;
    }

    img {
      width: 40px;
      height: 40px;
      border-radius: 100%;
      object-fit: cover;
    }
  }

  &__profileMenu {
    width: 230px;
    background-color: white;
    border-radius: 5px;
    box-shadow: 0px 5px 20px -5px rgba(black, 0.1);
    position: absolute;
    top: 80px;
    right: 20px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    transform: translateY(20px);
    transition: all 0.4s ease;
    visibility: hidden;
    opacity: 0;

    @include sm {
      top: 70px;
      right: 15px;
      padding: 10px;
    }

    &:before {
      content: "";
      position: absolute;
      width: 20px;
      height: 20px;
      border-radius: 1px;
      background-color: white;
      top: -9px;
      right: 30px;
      transform: rotate(45deg);

      @include sm {
        right: 47px;
      }
    }
  }

  &__profile {
    &:hover {
      .mainNav {
        &__profileMenu {
          visibility: visible;
          transform: translateY(0px);
          opacity: 1;
        }
      }
    }
  }

  &__profileLink {
    margin-bottom: 10px;
    color: $neu-08;
    font-size: 18px;
    font-weight: 400;
    padding: 8px;
    border-radius: 4px;
    display: flex;
    align-items: center;

    &:last-child {
      margin-bottom: 0px;
    }

    @include sm {
      &:first-child {
        margin-top: 5px;
      }
    }

    &:hover {
      background-color: rgba(0, 0, 0, 0.2);
    }

    svg {
      width: 20px;
      height: 20px;
      border-radius: initial;
      margin-right: 10px;
    }

    &.account {
      border-bottom: 1px solid rgba(black, 0.2);
      border-top-left-radius: 4px;
      border-top-right-radius: 4px;
      border-bottom-left-radius: 0;
      border-bottom-right-radius: 0;
    }
  }

  &__menuIcon {
    min-width: 40px;
    height: 40px;
    position: relative;
    display: none;
    cursor: pointer;
    z-index: 2;

    @include sm {
      display: block;
    }

    span {
      content: "";
      height: 2px;
      width: 25px;
      background-color: $m-02;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      position: absolute;
      border-radius: 1000px;
      transition: all 0.3s ease;

      &:after,
      &:before {
        background-color: $m-02;
        content: "";
        height: 100%;
        width: 100%;
        position: absolute;
        border-radius: 300px;
        transition: all 0.3s ease;
      }

      &:before {
        top: -8px;
        transition: all 0.3s 0.1s ease;
      }

      &:after {
        bottom: -8px;
      }
    }

    &:hover {
      span {
        &:before {
          width: 80%;
          transition: all 0.3s 0.1s ease;
        }

        &:after {
          width: 80%;
          bottom: -8px;
        }
      }
    }
  }

  &.sticky {
    color: $neu-08;

    &:before {
      opacity: 1;
      transform: translateY(0%);
    }

    .mainNav {
      &__logo {
        background-image: url("./assets/logos/logo-b.svg");

        @include xs {
          background-image: url("./assets/logos/logo-mobile-w.svg");
        }
      }

      &__search {
        input {
          background-color: rgba($neu-08, 0.1);
        }
      }

      &__upload {
        svg {
          g path {
            fill: $m-02;
          }
        }
      }
    }
  }

  // Menu Link Mobile

  &__mobileLink {
    text-transform: uppercase;
    font-size: 18px;
    letter-spacing: 1.5px;
    font-weight: 400;
    line-height: normal;
    color: $neu-08;
    transition: all 0.4s ease;
    cursor: pointer;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all 0.2s ease;

    &:before {
      content: "";
      position: absolute;
      width: 0px;
      height: 2px;
      background-color: $m-02;
      transition: all 0.2s ease;
    }

    &:hover {
      &:before {
        width: calc(100% + 4px);
      }
    }

    @include sm {
      margin-right: 0;
      font-size: 18px;
      margin-bottom: 60px;

      &:last-of-type {
        margin-bottom: 0;
      }
    }

    &:last-of-type {
      margin-bottom: 0;
    }
  }

  // Menu Mobile

  &__mobile {
    height: 100vh;
    position: fixed;
    background-color: rgba(white, 0.9);
    width: 80%;
    z-index: 3;
    right: -100%;

    @media screen and (min-width: 799px) {
      display: none;
    }

    a {
      z-index: 2;
    }

    &.open-menu {
      position: fixed;
      background-color: $m-02;
      transition: all 0.3s ease;
      right: 0;
      top: 0;
      display: flex;
      z-index: 4;
      width: 80%;
      height: 100vh;
      overflow-y: auto;
      box-shadow: 0 0 0 200px rgba(0, 0, 0, 0.3);
      animation: openMenu 2s ease forwards;

      &:before {
        content: "";
        display: inline-block;
        width: 120%;
        height: 100%;
        position: fixed;
        background-color: rgba(white, 0.9);
        backdrop-filter: blur(5px);
        animation: openMenu 2s 0.2s ease forwards;
      }

      @keyframes openMenu {
        0% {
          right: -100%;
          width: 0;
        }

        40% {
          right: 0;
          width: 85%;
        }

        100% {
          width: 80%;
          right: 0;
        }
      }
    }

    &.close-menu {
      animation: closeMenu 1.8s ease forwards;
      z-index: 4;

      @keyframes closeMenu {
        0% {
          right: 0;
          width: 80%;
        }

        30% {
          right: 0;
          width: 90%;
        }

        100% {
          width: 0%;
          right: -100%;
        }
      }
    }
  }

  &__mobileLinks {
    width: fit-content;
    margin: 0 auto;
    height: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
  }

  &__mobileLink {
    z-index: 3;
    left: 30%;

    &.anime-menu-links {
      animation: menu-links 1s ease forwards;
      opacity: 0;

      &:nth-child(1) {
        opacity: 0;
        animation-delay: 0.4s;
      }

      &:nth-child(2) {
        opacity: 0;
        animation-delay: 0.6s;
      }

      &:nth-child(3) {
        opacity: 0;
        animation-delay: 0.8s;
      }

      &:nth-child(4) {
        opacity: 0;
        animation-delay: 1s;
      }

      &:nth-child(5) {
        opacity: 0;
        animation-delay: 1.2s;
      }

      @keyframes menu-links {
        0% {
          opacity: 0;
          left: 30%;
        }

        100% {
          opacity: 1;
          left: 0;
        }
      }
    }

    &.active {
      color: $m-02;
    }
  }

  &__menuIcon.close {
    right: 2%;
    top: 10px;
    position: absolute;

    span {
      background-color: transparent;

      &:before {
        top: 0;
        transform: rotate(45deg);
        background-color: $m-02;
      }

      &:after {
        top: 0;
        transform: rotate(-45deg);
        background-color: $m-02;
      }
    }

    &:hover {
      span {
        &:before,
        &:after {
          top: 0;
          transform: rotate(0);
        }
      }
    }
  }
}

.mainHeading {
  height: 100vh;
  min-height: 700px;
  padding-left: 5vw;
  padding-right: 5vw;
  background-image: linear-gradient(
      180deg,
      #000000 0%,
      rgba(255, 255, 255, 0) 10%
    ),
    url("./assets/images/bg.jpg");
  background-size: cover;
  background-position: top;
  background-repeat: no-repeat;
  display: flex;
  align-items: center;
  overflow-x: hidden;

  @include sm {
    min-height: 500px;
    background-image: url("./assets/images/bg-mobile.jpg");
    justify-content: center;
  }

  &__text {
    user-select: none;
    position: sticky;
    right: 0;

    @include md {
      font-size: 90%;
    }

    @include sm {
      font-size: 70%;
    }

    @include xs {
      font-size: 45%;
    }

    @include xxs {
      font-size: 40%;
    }
  }

  &__title {
    font-size: 3em;
    font-weight: 400;
    margin-left: 4px;
    animation: enterLeft 1s $cubic backwards;
    letter-spacing: 0.62em;
    color: white;
    opacity: 0.85;
  }

  &__subtitle {
    text-transform: uppercase;
    font-size: 7em;
    color: $m-02;
    margin-left: -0.05em;
    line-height: 0.8em;
    display: block;
    padding: 0.2em 0px;
  }

  &__subtitleWord {
    @include delay;
    display: block;
    animation: enterLeft 1s $cubic both;
    background: $g-01;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;

    &:first-child {
      letter-spacing: 0.11em;
    }
  }

  &__description {
    margin-top: 2em;
    font-size: 2em;
    font-weight: 500px;
    position: relative;
    line-height: 2em;
    color: white;
    opacity: 0.85;

    @include xs {
      font-size: 16px;
    }

    &--line1,
    &--line2 {
      display: flex;
      animation: enterLeft 1s 0.8s $cubic both;
    }

    &--line2 {
      animation-delay: 1.1s;
    }

    &--strike {
      position: relative;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      margin-right: 0.9em;

      &:before {
        content: "";
        position: absolute;
        width: calc(100% + 8px);
        transform: translateY(2px);
        height: 0.1em;
        background-color: $m-02;
        animation: strike 0.4s 2s $cubic both;
        transition: all 0.4s $cubic;

        @keyframes strike {
          0% {
            width: 0;
          }
        }
      }

      &:hover {
        &:before {
          height: 80%;
        }
      }
    }

    &--super {
      position: absolute;
      color: $m-02;
      transform: rotate(-30deg) translate(0.5em, -2.2em) skew(-10deg, 5deg);
      border-left: 0.1em solid $m-02;
      border-bottom: 0.1em solid $m-02;
      padding: 0px 0px 5px 2%;
      //left: 16.5em;
      left: 12.5em;
      bottom: 0;
      line-height: 1.5em;
      transition: all 0.4s ease;
      animation: super 0.4s 2.4s $cubic backwards;

      @include sm {
        color: $m-02;
        border-left: 0.1em solid $m-02;
        border-bottom: 0.1em solid $m-02;
      }

      @include xs {
        left: 13.5em !important;
      }

      @keyframes super {
        0% {
          opacity: 0;
          transform: rotate(-80deg) translate(0em, -2.2em) skew(0deg, 0deg);
        }

        100% {
          opacity: 1;
          transform: rotate(-30deg) translate(0.5em, -2.2em) skew(-10deg, 5deg);
        }
      }

      @include xs {
        transform: rotate(-30deg) translate(-10px, -40px) skew(-10deg, 5deg);
        animation: none;
        left: 17em;
      }

      &:hover {
        transform: rotate(-20deg) translate(0.5em, -2.2em) skew(-10deg, 5deg);
      }
    }
  }

  &__scroll {
    width: 2.5em;
    height: 2.5em;
    background-size: cover;
    background-image: url("./assets/icons-base/triangle.svg");
    background-repeat: no-repeat;
    position: absolute;
    bottom: 64px;
    left: 50%;
    transform: translateX(-50%);
    animation: 0.8s ease-in-out both infinite alternate;
    animation-name: scrollDown;

    @include md {
      font-size: 90%;
    }

    @include sm {
      font-size: 70%;
    }

    @include xs {
      font-size: 45%;
    }

    @include xxs {
      font-size: 40%;
    }

    &:before,
    &:after {
      content: "";
      position: absolute;
      width: 100%;
      height: 100%;
      background-image: inherit;
      background-size: cover;
      background-repeat: no-repeat;
      opacity: 0.5;
      z-index: 2;
      top: -15px;
      left: 0;
      animation: inherit;
      animation-name: scrollDown2;
      animation-delay: 0.2s;
    }

    &:after {
      top: -30px;
      opacity: 0.2;
      animation-delay: 0.4s;
    }
  }

  @keyframes scrollDown {
    100% {
      transform: translateX(-50%) translateY(30px);
    }
  }

  @keyframes scrollDown2 {
    100% {
      opacity: 0;
      top: 0;
      transform: translateY(0px);
    }
  }
}

.videos {
  position: relative;

  &:before {
    content: "";
    position: absolute;
    width: 16%;
    max-width: 390px;
    height: 630px;
    background-color: transparent;
    border: solid 1px $m-02;
    left: 0;
    animation: beforeShape2 6s ease infinite alternate both;

    @keyframes beforeShape2 {
      0% {
        transform: translate(-50px, 0px);
      }

      100% {
        transform: translate(0px, 50px);
      }
    }

    @include md {
      display: none;
    }
  }

  &__heading {
    display: flex;
    justify-content: flex-end;
    align-items: center;
  }

  &__mainTitle {
    text-transform: uppercase;
    text-align: center;
    position: relative;
    display: inline-flex;
    font-weight: 900;
    font-size: 6em;
    line-height: 0;
    letter-spacing: 0.04em;
    color: transparent;
    position: relative;
    -webkit-text-fill-color: white;
    margin-top: -68px;
    opacity: 0;
    transform: translateY(160px);
    display: flex;
    justify-content: center;
    align-items: center;

    @include sm {
      margin-top: -24px;
    }

    &.anime {
      animation: itemsFade 1s ease forwards;

      @keyframes itemsFade {
        0% {
          opacity: 0;
          transform: translateY(160px);
        }

        100% {
          opacity: 1;
          transform: translateY(0px);
        }
      }

      &:before {
        animation: textBefore 1s 1s ease forwards;

        @keyframes textBefore {
          0% {
            opacity: 0;
            width: 0%;
          }

          100% {
            opacity: 1;
            width: 100%;
          }
        }
      }
    }

    &:before {
      position: absolute;
      content: "";
      width: 0%;
      height: 2px;
      top: 40%;
      background-color: $m-02;
      transition: all 0.4s ease;
    }

    @include sm {
      font-size: 32px;
    }
  }

  &__content {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    position: relative;
    margin-top: 0.5px;
    overflow: hidden;

    &:before {
      position: absolute;
      content: "";
      mix-blend-mode: multiply;
    }

    @include md {
      grid-template-columns: repeat(2, 1fr);
    }

    @include sm {
      grid-template-columns: 1fr;
    }
  }

  &__item {
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    cursor: pointer;
    width: calc(100% - 1px);
    z-index: 2;
    margin-bottom: 1px;
    height: 0;
    padding-bottom: 60%;
    @include anime;

    &:last-child {
      width: 100%;
    }

    @include sm {
      width: 100%;
      margin-bottom: 1px;
    }

    img {
      width: 100%;
      height: 100%;
      color: #202842a8;
      object-fit: cover;
      filter: grayscale(0%);
      transition: all 0.8s ease-out;
      position: absolute;
      top: 0;
    }
  }

  &__title {
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    opacity: 0;
    z-index: 3;
    color: transparent;
    -webkit-text-stroke: 1px $neu-08;
    font-weight: 900;
    font-size: 40px;
    letter-spacing: 1.5px;
    text-transform: uppercase;
    transition: all 0.6s ease;
    text-align: center;
    height: 100px;
    display: flex;
    justify-content: center;
    align-items: center;
    display: flex;
    justify-content: center;
    align-items: center;
    transition: all 0.4s ease;

    @include md {
      font-size: 28px;
    }

    @include sm {
      opacity: 1;
    }
  }

  &__icon {
    position: absolute;
    opacity: 0;
    color: white;
    transition: all 0.4s ease;
    z-index: 3;
    top: 50%;
    transform: translateY(calc(-50% + 100px));
    width: 32px;
    height: 32px;
    display: block;
    background-image: url("./assets/icons-base/icon-play.svg");
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;

    @include sm {
      transform: translateY(calc(-50% + 40px));
      opacity: 1;
    }
  }

  &__item {
    &:hover {
      .videos {
        &__title {
          color: white;
          transform: translateY(calc(-50% - 10px));
          opacity: 1;

          &:before {
            width: calc(100% + 24px);
            transition: all 0.4s 0.4s ease;
          }
        }

        &__icon {
          opacity: 1;
          transform: translateY(calc(-50% + 60px));

          @include sm {
            transform: translateY(calc(-50% + 40px));
          }
        }
      }

      img {
        // transform: scale(1.2);
      }
    }
  }
}

.backTop {
  width: 40px;
  height: 40px;
  border-radius: 100%;
  right: 24px;
  bottom: 24px;
  position: fixed;
  z-index: 2;
  opacity: 0;
  background: $g-01;
  transform: translateY(50px);
  transition: all 0.4s ease;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;

  @include sm {
    bottom: 68px;
  }

  &:before,
  &:after {
    content: "";
    position: absolute;
    width: 24px;
    height: 24px;
    background-image: url("./assets/icons-base/icon-arrowup.svg");
    background-size: contain;
    border-radius: 100%;
    z-index: 1;
    transition: all 0.3s ease;
  }

  &:after {
    transform: translateY(40px);
  }

  &:hover {
    &:before {
      transform: translateY(-40px);
    }

    &:after {
      transform: translateY(0px);
    }
  }

  &.show {
    opacity: 1;
    transform: translateY(0);
  }
}

.footer {
  width: 100%;
  height: 80px;
  bottom: 0;
  background-color: #f7f8fc;
  color: $neu-08;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 24px 40px;
  transition: all 0.4s ease;
  @include anime;

  @include sm {
    height: 120px;
    flex-direction: column;
    margin-bottom: 50px;
  }

  &__text {
    opacity: 0.75;

    @include sm {
      margin-bottom: 24px;
    }
  }

  &__icons {
    display: flex;
  }

  &__icon {
    width: 24px;
    height: 24px;
    background-size: contain;
    background-position: center;
    display: flex;
    justify-content: center;
    margin-right: 24px;
    cursor: pointer;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    position: relative;
    transition: all 0.2s ease;

    &:before {
      content: "";
      position: absolute;
      width: 0px;
      height: 2px;
      background-color: $m-02;
      transition: all 0.2s ease;
    }

    &:hover {
      &:before {
        width: calc(100% + 8px);
      }
    }

    p {
      position: absolute;
      opacity: 0;
      user-select: none;
    }

    &:last-child {
      margin-right: 0;
    }

    span {
      width: 100%;
      height: 100%;
      opacity: 0.6;
    }

    &.linkedin {
      span {
        background-image: url("./assets/icons-base/icon-linkedin-b.svg");
      }
    }

    &.dribbble {
      span {
        background-image: url("./assets/icons-base/icon-dribbble-b.svg");
      }
    }

    &.codepen {
      span {
        background-image: url("./assets/icons-base/icon-codepen-b.svg");
      }
    }

    &:hover {
      opacity: 1;
      transform: translateX(4px);

      span {
        opacity: 1;
      }

      &:before {
        opacity: 1;
      }
    }
  }
}

.controlMenu {
  display: flex;
  justify-content: space-between;
  align-items: center;
  position: fixed;
  z-index: 2;
  bottom: 0;
  background-color: rgba(white, 0.9);
  width: 100%;
  height: 50px;
  display: none;
  padding: 0 20px 0 20px;

  @include sm {
    display: flex;
  }

  @include xxs {
    padding: 0 10px 0 10px;
  }

  &__icon {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    font-family: $font-01;

    svg {
      width: 20px;
      height: 20px;
    }

    &.active {
      svg path {
        fill: $m-02;
      }
    }

    &:hover {
      svg path {
        fill: $m-02;
      }
    }
