}

.modal {
  z-index: 5;
  position: fixed;
  width: 100vw;
  height: 100vh;
  top: 0;
  left: 0;
  visibility: hidden;

  &:after,
  &:before {
    content: "";
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0;
    transition: all 0.4s ease;
    opacity: 0;
  }

  &:before {
    background-color: $neu-06;
  }

  &:after {
    background-color: $m-02;
    z-index: -1;
  }

  &__container {
    width: calc(70vw - 80px);
    max-width: 1280px;
    background-color: white;
    position: absolute;
    top: calc(50% + 100px);
    left: 50%;
    transform: translate(-50%, -50%) perspective(100em) rotateX(40deg);
    transition: all 0.4s ease;
    opacity: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 80px 0;

    @include sm {
      width: initial;
      height: initial;
      padding: 0;
      transform: translate(-50%, -50%) perspective(100em) rotateX(20deg);
    }
  }

  &__icon {
    width: 32px;
    height: 32px;
    position: fixed;
    right: 24px;
    top: 24px;
    cursor: pointer;
    transition: all 0.2s ease;

    svg {
      width: 100%;

      path {
        fill: #666666;
      }
    }

    &:hover {
      transform: rotate(90deg) scale(1.2);
    }

    @include sm {
      right: 20px;
      top: 20px;
    }
  }

  &__logo {
    position: absolute;
    bottom: 24px;
    width: 40px;
    height: 40px;

    img {
      width: 100%;
    }
  }

  &__video {
    width: calc(100% - 40px);
    margin: auto;

    @include sm {
      width: calc(100% - 40px);
    }
  }

  &__code {
    position: relative;
    padding-bottom: 56.25vh;
    padding-top: 30px;
    height: 0;
    overflow: hidden;

    @include sm {
      padding-bottom: 56.25%;
    }

    iframe {
      transform: translateY(50px);
      opacity: 0;
      transition: all 0.6s ease;
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
    }
  }

  &.open {
    transition: all 0.6s ease;
    visibility: visible;

    &:before {
      opacity: 0.8;
    }

    &:after {
      opacity: 0.2;
    }

    .modal {
      &__container {
        width: 70vw;
        opacity: 1;
        top: 50%;
        transform: translate(-50%, -50%) perspective(0em) rotateX(0deg);

        @include sm {
          width: calc(100% - 40px);
          height: auto;
          padding: 80px 0;
        }
      }

      &__code {
        iframe {
          transform: translateY(0px);
          background-color: $m-02;
          transition: all 0.4s 0.4s ease;
          opacity: 1;
        }
      }
    }
  }
}

